import Layout from "../components/layout";
import Seo from "../components/seo";
import SmartContractScreen from "../screens/smart-contract";
import React from "react";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";
import BreadCrumb from "../components/common/breadcrumb";
import MoveForward from "../components/common/move-forward";

const SmartContract = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Smart Contract Development Services", link: null },
  ];

  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <SmartContractScreen db={db} />
      <MoveForward db={db} />
    </Layout>
  );
};

export const Head = (props) => {
  const { data } = props;
  const fileUrl =
    data?.smartContractDevImg?.childImageSharp?.gatsbyImageData?.images
      ?.fallback?.src;
  return (
    <Seo
      title="Smart Contract Development Company | Justtry Technologies"
      metaTitle="Smart Contract Development Company | Justtry Technologies"
      description=" Justtry Tech is a smart contract development company that provides DeFi smart contract development services such as Binance smart contract development, Tron smart contract development, EOS smart contract development, and etc."
      image={fileUrl}
    ></Seo>
  );
};

export default SmartContract;

export const queryData = graphql`
  query MyQuery {
    smartContractDevImg: file(
      relativePath: { eq: "smart-contract-development.png" }
    ) {
      childImageSharp {
        gatsbyImageData
      }
    }
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
